.card {
    display: flex;
    background-color: var(--bg-color);
    padding: 4rem 2rem;
    gap: 8rem;
    margin: 1rem 0rem;
}

.projectInfo {
    gap: 0.5rem;
    flex: 1;
    display: flex;
    flex-direction: column;
}

.projectDescription {
    flex: 1.75;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}